html,
body {
	font-family: Helvetica, "sans-serif";
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

hr {
	border: unset;
	border-bottom: 1px solid #ccc;
}

.App {
	max-width: 700px;
}

.menu-list {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	margin-bottom: 1rem;
	border-bottom: 1px solid #eee;
	box-shadow: 1px 2px 5px #eee;
}

.menu-list-item.active {
	background-color: rgb(231, 255, 212);
}

.menu-list a {
	border-right: 1px solid #ccc;
	padding: 0.5rem 0.75rem;
	cursor: pointer;
	text-decoration: unset;
	color: unset;
	display: flex;
	flex: 1;
	border-left: unset;
	justify-content: center;
}

.menu-list a:last-child {
	border-right: unset;
}

.content {
	padding: 0 0.75rem 0;
}

.input {
	margin-top: 4px;
	border: 1px solid #eee;
	padding: 10px;
	display: flex;
	transition: all 0.3s;
}

.input.single-input--shifted {
	margin-left: 2rem;
	margin-top: 0;
	border-top: unset;
}

.input-number {
	margin-right: 8px;
	font-weight: bold;
}

.input-features {
	margin-left: auto;
	text-align: right;
}

.input-key {
	font-size: 0.8rem;
	margin-top: 4px;
	color: #aaa;
}

.input-layers {
	margin-top: 2px;
	font-size: 0.8rem;
	color: #4f4f4f;
}

.input-layers .input-number {
	margin-right: 4px;
	cursor: pointer;
}

.input-layers-inputs {
	margin-top: 2px;
	margin-left: 6px;
}

.buttons {
	display: flex;
}

.buttons button {
	margin-right: 8px;
	margin-bottom: 8px;
}

.bus-checkbox {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.bus-checkbox > input[type="checkbox"] {
	margin: 0 2px;
}

.bus-checkbox--large {
	width: 20px;
	height: 20px;
}
